import Vue from 'vue'
import App from './App.vue'
import './scss/app.scss';

import Default_Layout from './layouts/Default.vue'
import Dashboard_Layout from './layouts/Dashboard.vue'
import DashboardRTL_Layout from './layouts/DashboardRTL.vue'
import Antd from 'ant-design-vue';

Vue.config.productionTip = false
import VueRouter from 'vue-router'
import store from './store'

import VueAxios from 'vue-axios'
import VueCookies from 'vue-cookies';
import authHeader from './services/auth-header';

const axios = require('axios')
Vue.use(VueAxios, axios)
window.axios = require('axios')
import VueScrollTo from 'vue-scrollto'

Vue.use(VueScrollTo)


Vue.prototype.$urlapi = "http://127.0.0.1:8000/api/";
Vue.prototype.$url = "http://127.0.0.1:8000/";
window.axios = require('axios')
axios.defaults.baseURL = "http://127.0.0.1:8000/api/"
Vue.use(VueCookies);
import 'ant-design-vue/dist/antd.css';


axios.interceptors.request.use(function(config) {
    // console.log(authHeader())
    config.headers = authHeader()
    return config
})
// Adding template layouts to the vue components.
Vue.component("layout-default", Default_Layout);
Vue.component("layout-dashboard", Dashboard_Layout);
Vue.component("layout-dashboard-rtl", DashboardRTL_Layout);
import router from './router'

Vue.use(Antd);

Vue.use(VueRouter)
new Vue({
    router,
    store,
    render: h => h(App),
}).$mount('#app')
